'use client';
import React, { useState } from 'react';
import { API_BASE, DEV_TOKEN } from '../../../../lib/env';

export default function TokensPage({ params }: { params: Promise<{ id: string }> }) {
  const [pool, setPool] = useState('');
  const [to, setTo] = useState('');
  const [tokenId, setTokenId] = useState('1');
  const [amount, setAmount] = useState('1');
  const [resp, setResp] = useState<string | null>(null);

  async function call(path: string, body: any) {
    const org = await params;
    const res = await fetch(`${API_BASE}/api/besu/firefly/${org.id}/${path}`, {
      method: 'POST',
      headers: { 'content-type': 'application/json', authorization: `Bearer ${DEV_TOKEN}` },
      body: JSON.stringify(body),
    }).then((r) => r.json());
    setResp(JSON.stringify(res));
  }

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">ERC-1155 токены</h3>
      <div className="grid gap-3 md:grid-cols-2">
        <form onSubmit={(e) => { e.preventDefault(); call('tokens/pools', { type: 'fungible', name: pool || 'demo' }); }} className="border border-gray-800 rounded p-3">
          <div className="font-medium mb-2">Create Pool</div>
          <input className="border border-gray-700 rounded p-2 bg-gray-900 w-full" placeholder="pool name" value={pool} onChange={(e) => setPool(e.target.value)} />
          <button className="mt-2 px-3 py-2 rounded bg-indigo-600 text-white">Create</button>
        </form>
        <form onSubmit={(e) => { e.preventDefault(); call('tokens/transfer', { pool, tokenId: Number(tokenId), to, amount: Number(amount) }); }} className="border border-gray-800 rounded p-3">
          <div className="font-medium mb-2">Transfer</div>
          <div className="grid grid-cols-2 gap-2">
            <input className="border border-gray-700 rounded p-2 bg-gray-900" placeholder="pool" value={pool} onChange={(e) => setPool(e.target.value)} />
            <input className="border border-gray-700 rounded p-2 bg-gray-900" placeholder="to" value={to} onChange={(e) => setTo(e.target.value)} />
            <input className="border border-gray-700 rounded p-2 bg-gray-900" placeholder="tokenId" value={tokenId} onChange={(e) => setTokenId(e.target.value)} />
            <input className="border border-gray-700 rounded p-2 bg-gray-900" placeholder="amount" value={amount} onChange={(e) => setAmount(e.target.value)} />
          </div>
          <button className="mt-2 px-3 py-2 rounded bg-green-600 text-white">Send</button>
        </form>
      </div>
      {resp && <pre className="text-xs bg-gray-900 border border-gray-800 rounded p-3 whitespace-pre-wrap">{resp}</pre>}
    </div>
  );
}

